import React from 'react';
import firebase from 'firebase/app';
//import reactfire from 'reactfire'; //not used per mixin depreciation w/ES6

class RequestTrafficComponent extends React.Component {

    constructor(props) {
        super(props);
        this.data = [];
        this.pendingInitLoad = true;
        this.projectCode = 'glorious-frogs';
        this.dbRefStr = 'projectRequests/' + this.projectCode;
        if (!this.projectCode){throw "RequestTrafficComponent requires a project code be provided";}
    }

    componentWillMount() {
        let ref = firebase.database().ref(this.dbRefStr);
        ref.on('child_added', function(snap){
            console.log('RequestTraffic:child_added', snap);
            this.pendingInitLoad = false;
            this.data.push(snap.val());
            this.setState({
                pendingInitLoad: this.pendingInitLoad,
                data: this.data
            });
        }.bind(this));
    }

    //
    render() {
        return (
            <div className="RequestTrafficComponent" key={this.pendingInitLoad}>
                {this.pendingInitLoad ? (
                    <p>Waiting for data...</p>
                ):(
                    <div>
                        <p>Request count: ({this.data.length})</p>
                    </div>
                )}
            </div>
        );
    }
}
